.contentText {
  text-align: left;
  padding-left: 70px;
  font-weight: 600;
}
.info {
  height: 50px;
  display: flex;
  align-items: center;
  gap: 20px;
}
.avatar {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  object-fit: cover;
}
.blockImg {
  margin-left: 70px;
}
.contentImage {
  width: 100%;
  max-height: 500px;
  border-radius: 10px;
  object-fit: contain;
}
.nickName,
.date {
  opacity: 0.5;
}
.icons {
  display: flex;
  padding-left: 70px;
  justify-content: space-around;
  align-items: center;
}
